<template>
  <div>
    <h1>Select the pokemon you want to run calculations with</h1>
    <section class="list">
      <img
        v-for="(pokemon, key) in pokemonKeys"
        :class="{'marked': pokemon.selected === true}"
        @click="selectThisPokemon(pokemon)"
        :src="'/2d/'+pokemon.value+'.png'"
        :key="key"
      >
    </section>
    <button @click="requestNewPokemon()">Save</button>
  </div>
</template>

<style lang="postcss" scoped>
  .list {
    width: 37.5rem;
    height: 25rem;
    overflow: auto;
  }
  .marked {
    background-color: gold;
  }
</style>

<script>
import axios from "axios"
import battle from "@/mixins/battleMixin.js"

export default {
  mixins:[battle],
  props: ["pokemonKeys", 'tier', 'team'],
  data(){
      return {
          selectedPokemon : {},
          markedPokemon:{}    
      }
  },
  methods: {
    requestNewPokemon() {
      const keys = Object.keys(this.selectedPokemon);
      const level = this.getDefaultTierLevel(this.tier);
      const tier = this.tier;

      axios
        .post("/mysquad/retrieve", { keys: keys, level: level, tier: tier })
        .then(res => {
          this.selectedPokemon = {}
          this.setBattleResults(res.data.pokemon, this.team)
          this.$emit('close-my-modal')
        });
    },
    selectThisPokemon(pokemon){
      pokemon.selected = true
      this.selectedPokemon[pokemon.value] = true
    },
  }
};
</script>