<template>
  <section class="bmt__area">

    <div class="center bmt__menu">
      <a
        class="btn btn--diamond"
        v-for="(val, key) in componentAlphabet"
        :key="key"
        @click="updateView(componentList[val], val)"
        :class="{'btn--active':val === active}"
      >
        <span>{{val}}</span>
      </a>

      <a
        class="btn btn--diamond"
        @click="copyToClipboard()"
        v-if="$route.params.id"
      >
        <span>Export</span>
      </a>
      <a
        class="btn btn--diamond"
        @click="uploadTeam()"
        v-else
      >
        <span>Share</span>
      </a>
    </div>

    <keep-alive>
      <component
        class="bmt__big"
        :is='component'
        :data='parsed'
      ></component>
    </keep-alive>

    <transition name='bounce'>
      <Modal
        v-if="pokemonExportModal"
        @close-my-modal="pokemonExportModal = false"
        :sizeClass='"modal__content--large"'
      >
        <textarea
          class="modal__export"
          v-if="$route.params.id"
          :value="parsed.raw"
        ></textarea>
        <div v-else>
          <p>{{uploaded}}</p>
        </div>
      </Modal>
    </transition>

  </section>
</template>

<script>
import Modal from "@/components/Modal";
import TeamViewGeneral from "@/components/TeamView/_TeamView.vue";
import TeamViewCounter from "@/components/TeamView/_TeamViewCounter.vue";
import axios from "axios";

export default {
  components: {
    TeamViewCounter,
    TeamViewGeneral,
    Modal
  },
  data() {
    return {
      component: "TeamViewGeneral",
      active: "Viewer",
      componentList: {
        Viewer: "TeamViewGeneral",
        Calcs: "TeamViewCounter"
      },
      componentAlphabet: ["Viewer", "Calcs"],
      uploaded: false,
      pokemonExportModal: false
    };
  },
  props: ["parsed"],
  methods: {
    updateView(component, active) {
      this.component = component;
      this.active = active;
    },
    uploadTeam() {
      if (!this.uploaded) {
        let teamMembers = [];
        this.parsed.pokemon.forEach(pkmn => {
          teamMembers.push({ name: pkmn.data.slug });
        });

        axios
          .post("/mysquad/upload", {
            raw: this.parsed.raw,
            tier: this.parsed.tier,
            teamMembers: teamMembers
          })
          .then(res => {
            if (res.data.code === 1) {
              this.uploaded = window.location.hostname + res.data.message;
              this.pokemonExportModal = true;
            }
          })
          .catch(err => {
            alert(err);
          });
      } else {
        this.pokemonExportModal = true;
      }
    },
    copyToClipboard() {
      this.pokemonExportModal = true;
    }
  }
};
</script>