<template>
  <div>
    <section class="teamView__counter">
      <div
        class="teamView__counter__ally"
        v-for="(value, key) in Object.keys(battleResults.team)"
        :class="{'teamView__counter__ally--highlight':value === selectedAllyPokemon}"
        :key="key"
        @click="setSelectedAllyPokemon(value)"
      >
        <img :src="'/2d/'+value+'.png'">
        <p>{{value}}</p>
      </div>
    </section>

    <section class="teamView__battle">
      <section v-show="selectedAllyPokemon">
        <div
          class="teamView__battle__members"
          v-for="(match, key) in Object.keys(battleResults.team[selectedAllyPokemon]).slice((start*multiplier)-10, (end*multiplier))"
          :key="key"
        >
          <div
            @click="openView(battleResults.team[selectedAllyPokemon][match].targetedBy)"
            class="center teamView__battle__members--img"
          >
            <div><img :src="'/2d/'+battleResults.team[selectedAllyPokemon][match].targetedBy+'.png'"></div>
          </div>
          <div class="center teamView__battle__members--evs">
            {{battleResults.team[selectedAllyPokemon][match].evs}}
          </div>
          <div class="center teamView__battle__members--move">
            {{battleResults.team[selectedAllyPokemon][match].move}}
          </div>
          <div class="center teamView__battle__members--damage">
            {{battleResults.team[selectedAllyPokemon][match].damage.toFixed(2)}}
          </div>
        </div>

        <section class="center center--row">
          <div class="dexview__slider dexview__slider--med">
            <a
              class="dexview__slider__back"
              @click="go(-1)"
            ></a>
            <div class="dexview__slider__box">
              <p>{{end*multiplier}} / {{battleSize}}</p>
            </div>
            <a
              class="dexview__slider__go"
              @click="go(1)"
            ></a>
          </div>

          <a
            class="dexview__slider dexview__slider--ball dexview__slider--config"
            @click="resetCalculator()"
          >
          </a>
          <a
            class="dexview__slider dexview__slider--ball dexview__slider--add"
            @click="addToCalculation()"
          >
          </a>
          <a
            class="dexview__slider dexview__slider--ball dexview__slider--reset"
            @click="resetCalculator()"
          >
          </a>
        </section>

      </section>
    </section>
    
    <transition name="bounce">
      <Modal
        v-if="pokemonListModal"
        transition="bounce"
        @close-my-modal="pokemonListModal = false"
        :sizeClass='"modal__content--large"'

      >
        <PokemonList
          @close-my-modal="pokemonListModal = false"
          :pokemonKeys='pokemonKeys'
          :tier='data.tier'
          :team='this.data.pokemon'
        ></PokemonList>
      </Modal>
    </transition>

  </div>
</template>

<script>
import Modal from "@/components/Modal";
import PokemonList from "./Slots/_PokemonList";

import battleMixin from "@/mixins/battleMixin.js";

import axios from "axios";

export default {
  mixins: [battleMixin],
  props: ["data"],
  components: {
    Modal,
    PokemonList
  },
  data() {
    return {
      start: 10,
      end: 10,
      multiplier: 1,
      pokemonKeys: [],
      pokemonListModal: false
    };
  },
  methods: {
    openView(pkmn) {
      const tier = this.data.tier;
      console.log(tier);
      const routeData = this.$router.resolve({
        name: "pokemon",
        params: { tier: tier, pokemon: pkmn }
      });
      window.open(routeData.href, "_blank");
    },
    go(value) {
      value = this.multiplier + value;
      if (value <= 0) {
        return;
      }
      if (value > Math.ceil(this.battleSize / 10)) {
        return;
      }
      this.multiplier = value;
    },
    addToCalculation() {
      this.pokemonListModal = true;
      const tier = this.data.tier;
      axios.get(`/mysquad/${tier}`).then(res => {
        if (res.data.code) {
          this.pokemonKeys = res.data.keys;
        }
      });
    },
    resetCalculator() {
      this.setBattleResults(this.data.weak, this.data.pokemon);
    }
  },
  created() {
    this.setBattleResults(this.data.weak, this.data.pokemon);
  }
};
</script>