<template>
  <section class="modal" @click.self="closeMyModal">
    <section class="modal__content" :class="sizeClass">
      <div class="modal__bar">
        <button
        class="modal__close"
          @click="closeMyModal"
        >X</button>
      </div>
      <section class="modal__settings">
        <slot></slot>
      </section>
    </section>
  </section>
</template>
<script>
export default {
  name: "Modal",
  props:['sizeClass'],
  methods:{
    closeMyModal(e){
      e.stopPropagation()
      this.$emit("close-my-modal", false)
    }
  }
};
</script>
